import Products from "../../models/product.js";
import cancel from "../../../img/cancel.png";

class List  {
  constructor() {
    this.model = new Products();
    this.dataToSend = {}
  }

  async getData() {
      return new Promise(resolve => this.model.getProductsList().then(data => resolve(data)));
  }

  render(productsData) {
      return new Promise(resolve => {
      resolve(`
              ${this.getItemsHtml(productsData)}
                    `);
    });
  }

  afterRender(){
    this.setActions();
  }

  setActions() {
      const tg = window.Telegram.WebApp;
      const contentContainerInner = document.getElementsByClassName('content-container__inner')[0];
      contentContainerInner.addEventListener('click', event => this.setAddButtonsActions(event.target, tg))

      this.setTgActions(tg)
    }

  getItemsHtml(productsData) {
    const html = [];

     productsData.forEach((product, index) => {
      const imgUrl = this.imageExists(product.img) ? product.img : cancel;

      html.push(
      `
      <div class='item'>
        <img class="img" src=${imgUrl} alt="item_img">
        <p>${product.name} · <span class="bold">${product.countPoints}</span> балл(-ов)</p>
        <button class="add-btn" id="${index}" data-name="${product.name}">Добавить</button>
      </div>
      `
      )
    })

    return html.join('')
  }

  imageExists(image_url){
    const http = new XMLHttpRequest();
    try{
      http.open('HEAD', image_url, false);
      http.send();
    }catch (e) {
      return false
    }

    return http.status != 404;
  }

    setAddButtonsActions(target, tg) {
      switch (true) {
          case(target.classList.contains('add-btn')):

              if (this.dataToSend.bonusProductId && this.dataToSend.bonusProductId === target.id) {
                  tg.MainButton.hide()
                  target.classList.remove('add-btn-clicked');
              } else {
                  tg.MainButton.show();
                  target.classList.add('add-btn-clicked');
                  const addButtons = document.getElementsByClassName('add-btn');
                 for(let button of addButtons)  {
                     if (button.id !== target.id) button.classList.remove('add-btn-clicked');
                 }
              }

              if (this.dataToSend.bonusProductId) delete this.dataToSend.bonusProductId

              this.dataToSend.bonusProductId = target.id;

              tg.MainButton.setText(`Заказать товар ${target.dataset.name}`)

              console.log('this.dataToSend' + JSON.stringify(this.dataToSend))

              break;
      }
    }

    setTgActions() {

        Telegram.WebApp.onEvent('mainButtonClicked', () => {
            // this.model.sendOrderData(this.dataToSend.bonusProductId)
        })
    }

}


export default List;
